  ##transition: all 0.25s ease-out;
  -webkit-transform-origin: 15.6em 15.6em;
          transform-origin: 15.6em 15.6em;
}
.pie[data-v-0fe07f3d]:hover {
  fill: navy;
  ##transform: scale(1.1);
